
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'


var weatherbaseList=[];
export default {
components: {
XSwitch,
Group
},
created() {
var baseListStr=this.$route.query.content;
console.log("接收str==>"+baseListStr)
localStorage.setItem("baseList",baseListStr);
weatherbaseList=JSON.parse(baseListStr);
this.weatherList=weatherbaseList;
sessionStorage.setItem("isUp","false")
},
data() {
return {
weatherList:weatherbaseList,
title:{name:this.fontChanger(1,"区域管理"),flag:false}
};
},
methods: {
changeShowType(isshow,_index){
console.log(weatherbaseList);
sessionStorage.setItem("isUp","true")
localStorage.setItem("baseList",JSON.stringify(weatherbaseList));
},
inHome(index){
console.log("index==>"+index)
/*var redundancy=0;
for(var i=0;i<index;i++){
if(!weatherbaseList[i].isshow){
redundancy++;
}
}
console.log("冗余"+redundancy)*/
if(this.weatherList[index].isshow){
this.$router.push({path:'/Home',query: { paramindex: index }});
}else{
this.$toast({message:this.fontChanger(1,'不能查看未开启的城市天气'),duration:3000})
}
}
}
};



